
/* side nav bar styling */

nav {
    background: rgba(0,0,0,0.3);
    width: max-content;
    display: block;
    padding: 5rem 1.3rem;
    z-index: 2;
    position: fixed;
    left: 5%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: grid;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a{
    background:transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover {
    background: rgba(0,0,0,3);

}
nav a.active {
    background: var(--color-bg);
    color: var(--color-white);
}